/* Archive Container */
.archive-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
}

.archive-task {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 6px;
  padding: 6px;
  background-color: #2b2b2b;
  border: 1px solid #444444;
  border-radius: 5px;
  gap: 10px;
  width: 100%;
  max-width: 600px;
}

/* Clear Archive Button */
.clear-archive-button {
  background-color: #9a5c2b;
  color: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  cursor: pointer;
  margin-top: 10px;
  display: block;
  transition: all 0.2s ease-in-out;
}

.clear-archive-button:hover {
  background-color: #cf4452;
  transform: scale(1.01);
}
