/* Form Container */
.form-container {
  background-color: #2b2b2b;
  border: 1px solid #444444;
  border-radius: 8px;
  padding: 20px;
  margin: 20px auto;
  max-width: 600px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Form Header */
.form-header {
  margin-bottom: 15px;
  text-align: center;
}

.form-header h2 {
  font-size: 1.5rem;
  color: #d4af37ff;
}

/* Form Fields */
.form-field {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.form-field label {
  font-size: 1rem;
  color: #cccccc;
  margin-bottom: 5px;
}

.form-field input,
.form-field textarea,
.form-field select {
  background-color: #1a1a1a;
  color: #fafafa;
  border: 1px solid #444444;
  border-radius: 5px;
  padding: 10px;
  font-size: 1rem;
}

.form-field input:focus,
.form-field textarea:focus,
.form-field select:focus {
  outline: none;
  border-color: #b87333;
  box-shadow: 0 0 5px rgba(184, 115, 51, 0.5);
}

/* Form Buttons */
.form-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.form-buttons .btn {
  flex: 1;
}

/* Error Messages */
.form-error {
  color: #cf4452;
  font-size: 0.9rem;
  margin-top: 5px;
}

/* Add Task Form and Edit Task Form */
#add-task-form,
#edit-task-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 16px;
  border: 2px solid #b87333;
  border-radius: 8px;
  background-color: #292e3d;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Form Labels */
#add-task-form label,
#edit-task-form label {
  font-family: "Cinzel", serif;
  font-size: 1.1rem;
  letter-spacing: 0.5px;
  color: #d4af37ff;
  font-weight: 700;
}

/* Form Inputs, Textareas, and Selects */
#add-task-form input,
#add-task-form textarea,
#add-task-form select,
#edit-task-form input,
#edit-task-form textarea,
#edit-task-form select {
  padding: 10px;
  border: 1px solid #da8973;
  border-radius: 5px;
  background-color: #1a1a1a;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 600;
  transition: border-color 0.2s ease-in-out;
}

/* Hover Effect for Inputs */
#add-task-form input:hover,
#add-task-form textarea:hover,
#add-task-form select:hover,
#edit-task-form input:hover,
#edit-task-form textarea:hover,
#edit-task-form select:hover {
  border-color: #cf4452;
}

/* Form Buttons */
#add-task-form button,
#edit-task-form button {
  margin-top: 4px;
  background-color: #b87333;
  border: none;
  border-radius: 5px;
  color: #fafafa;
  font-family: inherit;
  font-weight: 700;
  font-size: 1.2rem;
  letter-spacing: 1px;
  padding: 10px;
  cursor: pointer;
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
}

/* Hover Effect for Buttons */
#add-task-form button:hover,
#edit-task-form button:hover {
  background-color: #cf4452;
  transform: scale(1.01);
}

#add-project-form {
  text-align: center;
  animation: slide-up 0.3s ease-in-out;
  color: #fafafa;
  font-family: inherit;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
