/* Modal Container */
.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1001;
}

.modal.hidden {
  display: none;
}

/* Modal Content */
.modal-content {
  background-color: #292e3d;
  border: 2px solid #b87333;
  border-radius: 10px;
  width: 100%;
  max-width: 500px;
  max-height: 85vh;
  overflow-y: auto;
  padding: 15px;
  margin: auto;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  text-align: left;
  animation: slide-up 0.3s ease-in-out;
  color: #fafafa;
  scrollbar-width: thin;
  scrollbar-color: #b87333 #292e3d;
}

.modal-content h2 {
  margin: 0 0 15px 0;
  color: #d4af37ff;
  font-size: 1.35rem;
  line-height: 1;
  text-align: center;
}

/* Webkit scrollbar styling */
.modal-content::-webkit-scrollbar {
  width: 8px;
}

.modal-content::-webkit-scrollbar-track {
  background: #292e3d;
}

.modal-content::-webkit-scrollbar-thumb {
  background-color: #b87333;
  border-radius: 4px;
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  margin-bottom: 5px;
}

.modal-header h3 {
  font-size: 1.2rem;
  line-height: 1;
  color: #d4af37ff;
}

.modal-actions {
  display: flex;
  gap: 10px;
}

/* Styles for .task-details when the modal is expanded */
.task-details.expanded {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 10px;
  text-align: left;
  font-size: 0.85rem;
  background-color: #313648;
  border-radius: 5px;
  color: #fafafa;
  margin: 0;
}

.task-details.expanded p {
  margin: 4px;
  letter-spacing: 0.5px;
}

.task-details.expanded strong {
  color: #d54352;
  letter-spacing: 0.5px;
  font-weight: 800;
}

/* Delete Confirmation Modal Content */
#delete-confirmation-modal .modal-content {
  background-color: #292e3d;
  border: 2px solid #b87333;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  text-align: center;
  animation: all 0.3s ease-in-out;
  color: #fafafa;
}

/* Delete Confirmation Modal Heading */
#delete-confirmation-modal h3 {
  font-family: "Cinzel", serif;
  font-size: 1.5rem;
  color: #d4af37ff;
  margin-bottom: 10px;
}

/* Delete Confirmation Modal Description */
#delete-confirmation-modal p {
  font-family: inherit;
  font-size: 0.9rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  color: #d5d5d5;
  margin-bottom: 20px;
}

/* Task Name in Description */
#delete-confirmation-modal strong {
  color: #cf4452;
  font-weight: 700;
}

/* Add Project Modal Content */
#add-project-modal .modal-content {
  background-color: #292e3d;
  border: 2px solid #b87333;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 20px 24px;
  margin: auto;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  text-align: left;
  animation: slide-up 0.3s ease-in-out;
  color: #fafafa;
  font-family: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

#add-project-modal .modal-content h2,
#add-project-modal .modal-content h3 {
  margin: 0 0 15px 0;
  color: #d4af37ff;
  font-size: 1.3rem;
  line-height: 1;
  text-align: center;
}

#add-project-modal .modal-content input[type="text"] {
  padding: 0.75rem 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  background: #313648;
  color: #fafafa;
}

#add-project-modal .modal-content .modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

#add-project-modal .modal-content button {
  padding: 0.5rem 1.2rem;
  border-radius: 6px;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  background: #b87333;
  color: #fff;
  transition: all 0.2s ease-in-out;
}

#add-project-modal .modal-content button:hover {
  background: #cf4452;
}

/* Animations */
@keyframes slide-up {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
