/* Bottom Navigation Bar */
.bottom-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(135deg, #292e3df0, #282d3cf0);
  border-top: 2px solid #212121;
  padding: 12px 0;
  z-index: 999;
}

.nav-btn {
  background: none;
  border: none;
  color: #ccc;
  font-size: 1.075rem;
  font-weight: bold;
  font-family: "Dalek Pinpoint", sans-serif;
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: color 0.25s ease-in-out;
  flex: 1;
}

.nav-btn#github-link {
  flex: 0.5;
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 0.85rem;
  text-decoration: none;
  opacity: 0.8;
  padding: 0 8px;
}

.nav-btn#github-link:hover {
  opacity: 1;
}

.nav-btn#github-link i {
  width: 14px;
  height: 14px;
}

.nav-btn.active {
  color: #cc7f37;
  font-weight: bold;
}

.nav-btn:hover {
  color: #fb8136;
}
