/* Base Button Styles */
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b87333;
  color: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 8px;
  width: fit-content;
  aspect-ratio: 1 / 1;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

/* Hover Effect */
.btn:hover {
  background-color: #cf4452;
  transform: scale(1.02);
}

/* Floating Add Task Button */
.floating-btn {
  position: fixed;
  bottom: 60px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: #b33f4a;
  color: #fafafa;
  font-size: 2.25rem;
  font-weight: 900;
  line-height: 0;
  border: 3px solid rgba(140, 50, 75, 0.75);
  border-radius: 25%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.4);
  transition: all 0.2s ease-in-out;
}

/* Hover Effect */
.floating-btn:hover {
  transform: scale(1.01);
  background-color: #cf4452;
  border: transparent;
  box-shadow: 0px 6px 8px rgba(207, 68, 82, 0.55);
}

/* Clear Archive Button */
.clear-archive-button {
  background-color: #9a5c2b;
  color: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 20px;
  display: block;
}

.clear-archive-button:hover {
  background-color: #b87333;
}

.edit-icon {
  color: #b87333;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.delete-icon {
  color: #cf4452;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

/* Base Styles for Confirmation Buttons */
.confirmation-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.confirmation-buttons button {
  flex: 1;
  background-color: #b87333;
  border: none;
  border-radius: 5px;
  color: #fafafa;
  font-family: inherit;
  font-weight: 800;
  font-size: 1rem;
  letter-spacing: 0.5px;
  padding: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

/* Cancel Button */
#cancel-delete-btn {
  background-color: #444444;
}

#cancel-delete-btn:hover {
  background-color: #666666;
}

/* Confirm Button */
#confirm-delete-btn {
  background-color: #cf4452;
}

#confirm-delete-btn:hover {
  background-color: #e74c3c;
}

#add-project-form button {
  font-weight: 900;
  margin: 0 5px;
}

.add-project-button {
  font-size: 50px;
}
