:root {
  /* Priority Colors */
  --priority-high: #dd5a33; /* Red-Bronze */
  --priority-medium: #d8b74b; /* Golden */
  --priority-low: #50c878; /* Emerald Green */

  /* Other Global Variables */
  --primary-color: #b87333; /* Warm Bronze */
  --secondary-color: #fafafa; /* Off-White */
  --background-color: #1a1a1a; /* Dark Background */
}

html,
body {
  height: 100%;
  margin: 0;
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #2f4375;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  padding: 15px;
}

/* Box Sizing */
* {
  box-sizing: border-box;
}

/* Hidden Utility Class */
.hidden {
  display: none !important;
}

/* Container Layout */
.container {
  background: linear-gradient(135deg, #292e3def, #282d3cef);
  border: 3px solid #252833;
  border-radius: 8px;
  padding: 20px;
  width: 100%;
  max-width: 700px;
  min-height: 500px;
  height: auto;
  box-shadow: 0 4px 10px 6px rgba(0, 0, 0, 0.3);
  text-align: center;
  overflow-y: auto;
  margin: 10px 0 50px;
}
